import { axios } from '@/utils/http'

export function getStudentList(params) {
    const url = '/apply_student/getStudent';
    return axios.get(url, {
        params
    })
}

export function deleteStudentById(params) {
    const url = '/apply_student/delStudent';
    return axios.post(url, params)
}

export function addStudent(params) {
    const url = '/apply_student/addStudent';
    return axios.post(url, params)
}

export function getTeacherList() {
    const url = '/market_teacher/getTeacher';
    return axios.get(url)
}

export function getOrgCodeList() {
    const url = '/org_chart/getDepartment';
    return axios.get(url)
}